import React from "react";
import { makeStyles } from "@material-ui/core/styles";
import Stepper from "@material-ui/core/Stepper";
import Step from "@material-ui/core/Step";
import StepLabel from "@material-ui/core/StepLabel";
import StepContent from "@material-ui/core/StepContent";
import Button from "@material-ui/core/Button";
import Paper from "@material-ui/core/Paper";
import Typography from "@material-ui/core/Typography";

const useStyles = makeStyles((theme) => ({
  root: {
    width: "100%",
  },
  button: {
    marginTop: theme.spacing(1),
    marginRight: theme.spacing(1),
  },
  actionsContainer: {
    marginBottom: theme.spacing(2),
  },
  resetContainer: {
    padding: theme.spacing(3),
  },
  buttons: {
    sell: "#76ff03",
    buy: "#ffc107",
    hold: "#35baf6",
  },
}));

function getSteps() {
  return ["Question No.1", "Question No.2", "Question No.3"];
}

function saveQuizInfo(e) {
  console.log(e.currentTarget.getAttribute('data-value'), e.currentTarget.getAttribute('data-step'))

  let step = e.currentTarget.getAttribute('data-step')
  let value = e.currentTarget.getAttribute('data-value')
  let data = [step, value]
  console.log(step)
  localStorage.setItem(`q${step}`, data)

  // console.log(localStorage.getItem(`q${step}`) + '< local storage')
  return console.log('ran')
}

function sendQuizInfo(){
  console.dir(  console.log(localStorage.getItem('q0'),localStorage.getItem('q1'),localStorage.getItem('q2'))
  , { depth: null });

  fetch('http://localhost:3001/get', {
    method: "post",
    headers: {
      Accept: "application/json",
      "Content-Type": "application/json",
    },

    //make sure to serialize your JSON body
    body: JSON.stringify({
      q_array: [localStorage.getItem('q0'),localStorage.getItem('q1'),localStorage.getItem('q2')]
    }),
  });
  return console.log('q_array sent.')
}

function getStepContent(step) {
  switch (step) {
    case 0:
      return `Do you like Jorge?`;
    case 1:
      return "Is Jorge Handsome?";
    case 2:
      return `How would you rate Jorge?`;
    default:
      return "Unknown step";
  }
}

export default function VerticalLinearStepper(props) {
  const classes = useStyles();
  const [activeStep, setActiveStep] = React.useState(0);
  const steps = getSteps();

  const handleNext = () => {
    setActiveStep((prevActiveStep) => prevActiveStep + 1);
  };

  const handleBack = () => {
    setActiveStep((prevActiveStep) => prevActiveStep - 1);
  };

  const handleReset = () => {
    setActiveStep(0);
  };

  return (
    <div className={classes.root} style={{ maxWidth: 400 }}>
      <Stepper activeStep={activeStep} orientation="vertical">
        {steps.map((label, index) => (
          <Step key={label}>
            <StepLabel>{label}</StepLabel>
            <StepContent>
              <Typography>{getStepContent(index)}</Typography>
              <div className={classes.actionsContainer}>
                <div>
                  <Button
                    variant="contained"
                    style={{ backgroundColor: "#ffc107" }}
                    onClick={(e) => {handleNext(); saveQuizInfo(e);}}
                    className={classes.button}
                    data-value="buy"
                    data-step={activeStep}

                  >
                    Buy
                  </Button>
                  <Button
                    variant="contained"
                    style={{ backgroundColor: "#4caf50" }}
                    onClick={(e) => {handleNext(); saveQuizInfo(e);}}
                    className={classes.button}
                    data-value="sell"
                    data-step={activeStep}
                  >
                    Sell
                  </Button>
                  <Button
                    variant="contained"
                    style={{ backgroundColor: "#35baf6" }}
                    onClick={(e) => {handleNext(); saveQuizInfo(e);}}
                    className={classes.button}
                    data-value="hold"
                    data-step={activeStep}

                  >
                    Hold
                  </Button>
                  <br />

                  <Button
                    disabled={activeStep === 0}
                    onClick={handleBack}
                    className={classes.button}
                  >
                    Back
                  </Button>

                  <Button
                    variant="contained"
                    color="primary"
                    onClick={handleNext}
                    className={classes.button}
                  >
                    {activeStep === steps.length - 1 ? "Finish" : "Next"}
                  </Button>
                </div>
              </div>
            </StepContent>
          </Step>
        ))}
      </Stepper>
      {activeStep === steps.length && (
        <Paper square elevation={0} className={classes.resetContainer}>
          <Typography>All steps completed - you&apos;re finished</Typography>
          <Button onClick={handleReset} className={classes.button}>
            Reset
          </Button>
          <Button onClick={() => {props.end(); sendQuizInfo();}} className={classes.button}>
            Save
          </Button>
        </Paper>
      )}
    </div>
  );
}
